ks-order-summary-field {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    gap: 5px 10px;
    padding: 15px 20px;
    background-color: #f2f2f2;

    margin-bottom: 5px;
    border-radius: 10px;
    text-align: left;
}

ks-order-summary-field[dark] {
    background-color: #151515;
    color: white;
}

ks-order-summary-field[single] {
    justify-content: center;
    text-align: center;
}

ks-order-summary-field[large] {
    font-weight: 700;
    font-size: 18px;
    font-family: var(--font-emphasis);
}