import { Component, h, Prop, Host } from '@stencil/core';

@Component({
  tag: 'ks-order-summary-field',
  styleUrl: 'summaryField.css'
})
export class OrderSummaryField {
    @Prop({reflect: true}) dark: boolean = false;
    @Prop({reflect: true}) large: boolean = false;
    @Prop({reflect: true}) single: boolean = false;

    render() {
        if(this.single)
            return <Host><slot /></Host>

        return <Host>
            <div><slot name="left" /></div>
            <div><slot name="right" /></div>
        </Host>;
    }
}